import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, NgModel, ReactiveFormsModule } from '@angular/forms';
import { HttpModule, JsonpModule } from '@angular/http';
import { FlexLayoutModule } from "@angular/flex-layout";
import { AppComponent } from './app.component';
import { StoreModule, combineReducers } from "@ngrx/store";
import {
  MdIconRegistry, MdSidenavModule, MdCardModule, MdMenuModule, MdSelectModule, MdButtonModule,
  MdCheckboxModule, MdInputContainer, MdInputModule, MdProgressSpinner, MdSlideToggleModule, MdButtonToggleModule,
  MdAutocompleteModule, MdTooltipModule, MdIconModule, MdListModule, MdSnackBarModule, MdProgressSpinnerModule,
  MdProgressBarModule,
  MdToolbarModule, MdProgressBar, MdTab, MdTabsModule, MdChipsModule
} from '@angular/material';
import 'hammerjs';
import 'hammer-timejs';
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { AgmCoreModule , GoogleMapsAPIWrapper } from '@agm/core';
import { AppRoutingModule } from "./router-module/app-router.module";
import { LocalStorageModule } from "angular-2-local-storage";
import { RouterStoreModule } from "@ngrx/router-store";

import { mainReducer } from "./reducers/main-store";
import { Angular2SocialLoginModule } from "angular2-social-login";
import {GOOGLE_PROVIDERS} from "../environments/environment";
import {FretronLogin} from "./components/fretron-login/fretron-login.component";
import {FretronSignup} from "./components/fretron-signup/fretron-signup.component";
import {LoginServices} from "./services/login-services";
import { ValidateOTP } from './components/validate-otp/validate-otp.component';
import { AddOrganisationComponent } from './components/add-organisation/add-organisation.component';
import {LogoutServices} from "./services/logout-services";

@NgModule({
  declarations: [
    AppComponent,
    FretronLogin,
    FretronSignup,
    ValidateOTP,
    AddOrganisationComponent,
  ],
  imports: [
    LocalStorageModule.withConfig({
      prefix: 'my-app',
      storageType: 'localStorage'
    }),
    BrowserModule,
    FormsModule,
    HttpModule,
    AppRoutingModule,
    JsonpModule,
    // MaterialModule,
    StoreModule.provideStore(mainReducer),
    RouterStoreModule.connectRouter(),
    BrowserAnimationsModule,
    AgmCoreModule.forRoot({
      // apiKey: 'AIzaSyD2ufpAUX67jwbWByvEeWX4Iee44MZhuR0'
      apiKey: 'AIzaSyBCRowVqghQTO8iJDgOc4QY-GlFwsn85_I',
      libraries: ["places"]
    }),
    FlexLayoutModule,
    MdCardModule,
    MdInputModule,
    MdSlideToggleModule,
    MdSelectModule,
    MdSidenavModule,
    MdButtonModule,
    MdCheckboxModule,
    MdMenuModule, MdButtonToggleModule, MdAutocompleteModule, ReactiveFormsModule, MdTooltipModule,
    MdIconModule,
    MdListModule,
    MdProgressBarModule,
    MdSnackBarModule, MdProgressSpinnerModule, MdToolbarModule,
    MdTabsModule,MdChipsModule,Angular2SocialLoginModule
    // GooglePlaceModule,
    // MdInputContainer,
  ],
  providers: [MdIconRegistry,
    NgModel,
    StoreModule,

    // MdInputModule,
    MdInputContainer,
    BrowserAnimationsModule,
    // AuthenticationServices,
    // DeviceServices,
    MdProgressSpinner,
    MdProgressBar,
    MdTab,
    GoogleMapsAPIWrapper,LoginServices,
    LogoutServices
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }

//FOR google auth // (login with google)
Angular2SocialLoginModule.loadProvidersScripts(GOOGLE_PROVIDERS);
//close google auth // (login with google)



