import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {FretronLogin} from "../components/fretron-login/fretron-login.component";
import {FretronSignup} from "../components/fretron-signup/fretron-signup.component";
import {ValidateOTP} from "../components/validate-otp/validate-otp.component";
import {AddOrganisationComponent} from "../components/add-organisation/add-organisation.component";

const routes: Routes = [
  {path: '', redirectTo: 'fretronLogin', pathMatch: 'full'},
  {path: 'fretronLogin', component: FretronLogin},
  {path: 'fretronSignup', component: FretronSignup},
  {path: 'validateOTP', component: ValidateOTP},
  {path: 'addOrganisation', component: AddOrganisationComponent},
  // {path: 'dashboard', component: Dashboard},
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
  constructor() {
  }
}



