import {Component} from '@angular/core';
import { Router} from "@angular/router";
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";
import {go, show, RouterState} from "@ngrx/router-store";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";
import {ObservableMedia} from "@angular/flex-layout";
// import {config} from "../../../../environments/environment";
import {AuthService} from "angular2-social-login";
import {GOOGLE_PROVIDERS} from "../../../environments/environment.prod";
import {LoginServices} from "../../services/login-services";

@Component({
  selector: 'app-fretron-login',
  templateUrl: './fretron-login.component.html',
  styleUrls: ['./fretron-login.component.css']
})

export class FretronLogin {
  googleClientID = GOOGLE_PROVIDERS.google.clientId;
  public isSpinner=false;
  public mobileNo:any='';
  public countryCode:any='+91';
  public countryCodeList:any=[{countryID:'IND',countryCode:'+91'}];
  public selectedLanguage='en';
  public unSub_userInfo:any=null;
  public supportedLangs: any=[ { name: 'English', code: 'en' },{ name: '', code: 'hi' }
    // { name: 'Espaol', code: 'es' },
    // { name: '', code: 'zh' },
  ];

  ngOnInit() {
    // this.selectedLanguage= (this.localStorageService.get("LANGUAGE"))?(""+this.localStorageService.get("LANGUAGE")):'en';
    // this.selectLang();
    // console.log(this.googleClientID);
    // AppGlobals.GOOGLE_CLIENT_ID = this.googleClientID;
  }

  ngOnDestroy(){
    if (typeof this.unSub_userInfo != 'undefined' && this.unSub_userInfo != null && this.unSub_userInfo != undefined)
      this.unSub_userInfo.unsubscribe();
  }


  selectLang() {
    // this._translate.use(this.selectedLanguage);
  }

  constructor(private _loginServices:LoginServices,public _auth: AuthService,public media: ObservableMedia,public snackBar: MdSnackBar,private _store:Store<any>,private localStorageService: LocalStorageService) {

    // dispatched from login-services
    this.unSub_userInfo =  this._store.select('userInfo').subscribe(value => {
      if(value==null){
        return;
      }
      this.isSpinner =false;
      console.log(value);
      if(value['error']==''){
        if( value['res']['userType'] =='new'){
          this._store.dispatch({type: "SIGNUP_INFO", payload:null});
          this._store.dispatch(go(['/fretronSignup']));
        }else if(value['res']['userType'] =='existing'){
          this._store.dispatch({type: "SIGNUP_INFO", payload:null});
          if(value['res']['loginType'] =='fretron'){
            this._store.dispatch(go(['/validateOTP']));
          }else if(value['res']['loginType'] =='google'){

            let token =value['res']['token'];
            console.log(token);
            this.localStorageService.remove("AUTH_TOKEN");
            // this.localStorageService.clearAll();
            this.localStorageService.set("AUTH_TOKEN",""+value['res']['token']);
            let user = this.customerDetail(value['res']['token']);
            let path='http://'+window.location.hostname+':'+window.location.port;
            console.log(path+"/transporter");
            if(user['details']['orgType'] =='TRANSPORTER'){
              window.location.replace(path+"/transporter");
            }else if(user['details']['orgType'] =='FLEET_OWNER'){
              window.location.replace(path+"/tracknet/vtsdashboard");
            }else if(user['details']['orgType'] =='FLEET_OWNER_AGENT'){
              // window.location.replace(path+"/tracknet/vtsdashboard");
            }
            else if((user['details']['orgType'] == undefined)|| (user['details']['orgType'] == null))
            {
              this._store.dispatch(go(['/addOrganisation']));               // window.location.replace("http://localhost:8080/tracknet/vtsdashboard");
            }
          }
        }
      }else{
         this.openSnackBar(value['errorMsg']);
      }
    });
  }//constructor close

  customerDetail(token){
    let userDetails ={};
    let base64Url = token.split('.')[0];
    let base64 = base64Url.replace('-', '+').replace('_', '/');
    userDetails['userType']=JSON.parse(window.atob(base64));

    base64Url = token.split('.')[1];
    base64 = base64Url.replace('-', '+').replace('_', '/');
    userDetails['details']=JSON.parse(window.atob(base64));
    userDetails['details']['id'] =userDetails['details']['userId'];

    console.log(userDetails);
    return userDetails;
  }

  checkExistingUser(){
    if(this.fieldsValidation()){
      let reqObj ={
        loginType:"fretron",
        mobileNumber:""+this.mobileNo
      };
      console.log(reqObj);
      this._loginServices.getUserInfo.emit(reqObj); // call api for checking user existing or not
    }
  }// login methods close


  fieldsValidation(){
    if(this.mobileNo==''  ||this.mobileNo==undefined){
      console.log("please fill mobile no");
      this.openSnackBar("please fill mobile no");
      return false;
    }
    return true;
  }

  loginWithGoogle(){
    console.log("google auth");
    // this.isSpinner=true;
    this._auth.login('google').subscribe(
      (data) => {
        // this.isSpinner=false;
        console.log(data);
        //user data
        //name, image, uid, provider, uid, email, token (accessToken for Facebook & google, no token for linkedIn),
        // idToken(only for google)

        let reqObj ={
          "loginType":"google",
          "authToken":{
            "token":data['token'],
            "email":data['email'],
            "name":data['name']
          }
        };
        console.log(reqObj);
        this.isSpinner=true;
        this._loginServices.getUserInfo.emit(reqObj); // call api for checking user existing or not
      },err=>{
        this.isSpinner=false;
        console.log(err);
      });
  }

  openSnackBar(message: string){
    this.snackBar.open(message, '', <MdSnackBarConfig>{duration :  3000});
  }

}// class close




