import { Component, OnInit } from '@angular/core';
import {LocalStorageService} from "angular-2-local-storage";
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";
import { Router} from "@angular/router";
import {MdSnackBarConfig, MdSnackBar} from "@angular/material";
import {LoginServices} from "../../services/login-services";

@Component({
  selector: 'app-fretron-signup',
  templateUrl: './fretron-signup.component.html',
  styleUrls: ['./fretron-signup.component.css']
})
export class FretronSignup implements OnInit {

  public name ='';
  public email ='';
  public mobileNo ='';
  public userType:any='';
  public isResend:boolean =false;
  public isSpinner:boolean =false;
  public stopOtpTimer:any;
  public loginType:any ='';
  public min =5;
  public sec =0;
  public unSub_userInfo:any =null;
  public unSub_signupInfo:any =null;

  ngOnDestroy(){
    if (typeof this.unSub_userInfo != 'undefined' && this.unSub_userInfo != null && this.unSub_userInfo != undefined)
      this.unSub_userInfo.unsubscribe();

    if (typeof this.unSub_signupInfo != 'undefined' && this.unSub_signupInfo != null && this.unSub_signupInfo != undefined)
      this.unSub_signupInfo.unsubscribe();
  }

  constructor(private _loginServices:LoginServices,public snackBar: MdSnackBar,private localStorageService: LocalStorageService,private _store:Store<any>,private _router:Router) {
    // let mob =this.localStorageService.get("USER-MOB-NO");
    // this.mobileNo =""+mob;
    // this.startTimer(300);

    this.unSub_userInfo =  this._store.select('userInfo').subscribe(value => {
      if(value==null){
        this.cancel();
        return;
      }
      console.log(value);

      if(value['error'] =='' && value['res']['userType'] =='new'){// if user not exist
        this.openSnackBar("Please Signup first");
        this.loginType = value['res']['loginType'];
        if(this.loginType =='google'){
            this.email = value['res']['email'];
            this.name = value['res']['name'];
        }else if(this.loginType =='fretron'){
          this.mobileNo = value['res']['mobileNumber'];
        }
      }
      // this.userType =value['res']['userType'];
    });

    this.unSub_signupInfo =  this._store.select('signupInfo').subscribe(value => {
      if(value ==null){
        return;
      }

      if(value['error'] ==''){
        this._store.dispatch(go(['/validateOTP']));
      }
    });



  }// close constructor

  ngOnInit(){

  }


  userSignup(){ //when user not exist and user signup
    console.log("sign up");
    if(this.fieldsValidation()){
      let obj ={
        "onBoardingType":this.loginType,
        "email":this.email,
        "mobileNumber":this.mobileNo,
        "name":this.name,
        "otpType":'signup'
      };
      // this._store.dispatch({type: "SIGNUP", payload:obj});
      this._loginServices.getSignupOTP.emit(obj); // call api for checking user existing or not
      console.log(obj);
    }
  }

  fieldsValidation(){
    if(this.name==''  || this.name ==undefined){
      console.log("please fill Name");
      return false;
    }

     if((this.email==''  || this.email==undefined ) && this.loginType=='google'){
      console.log("please fill Email");
      return false;
    }

    if(this.mobileNo==''  ||this.mobileNo==undefined){
      console.log("please fill Mobile No");
      return false;
    }

    return true;
  }

  cancel(){
    this._store.dispatch({type: "USER_INFO", payload:null});
    this._store.dispatch(go(['/fretronLogin']));
  }

  openSnackBar(message: string) {
    this.snackBar.open(message, '', <MdSnackBarConfig>{duration :  3000});
  }
} //class close



