import { Component, OnInit } from '@angular/core';
import {LocalStorageService} from "angular-2-local-storage";
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";
import { Router} from "@angular/router";
import {MdSnackBarConfig, MdSnackBar} from "@angular/material";
import {LoginServices} from "../../services/login-services";

@Component({
  selector: 'app-validate-otp',
  templateUrl: './validate-otp.component.html',
  styleUrls: ['./validate-otp.component.css']
})
export class ValidateOTP implements OnInit {
  public otp ='';
  public userInfo ={};
  public signupInfo ={};
  public isResend:boolean =false;
  public isSpinner:boolean =false;
  public stopOtpTimer:any;
  public min =5;
  public sec =0;
  public unSub_userInfo:any =null;
  public unSub_signupInfo:any =null;
  public unSub_resendOTP:any =null;
  public unSub_token:any =null;


  ngOnDestroy(){
    if (typeof this.unSub_userInfo != 'undefined' && this.unSub_userInfo != null && this.unSub_userInfo != undefined)
      this.unSub_userInfo.unsubscribe();

    if (typeof this.unSub_signupInfo != 'undefined' && this.unSub_signupInfo != null && this.unSub_signupInfo != undefined)
      this.unSub_signupInfo.unsubscribe();

    if (typeof this.unSub_resendOTP != 'undefined' && this.unSub_resendOTP != null && this.unSub_resendOTP != undefined)
      this.unSub_resendOTP.unsubscribe();

    if (typeof this.unSub_token != 'undefined' && this.unSub_token != null && this.unSub_token != undefined)
      this.unSub_token.unsubscribe();
  }

  constructor(private _loginServices:LoginServices,public snackBar: MdSnackBar,private localStorageService: LocalStorageService,private _store:Store<any>,private _router:Router) {
    // let mob =this.localStorageService.get("USER-MOB-NO");
    // this.mobileNo =""+mob;
    // this.startTimer(300);

    this.unSub_userInfo =  this._store.select('userInfo').subscribe(value => {
      if(value==null){
        this._store.dispatch({type: "USER_INFO", payload:null});
        this._store.dispatch({type: "SIGNUP_INFO", payload:null});
        this._store.dispatch(go(['/fretronLogin']));
        return;
      }
      // console.log(value);
       if(value['error'] ==''){// if user exist
         this.startTimer(300);
        this.openSnackBar("OTP send to your Mobile Number");
          this.userInfo = value['res'];
      }
      else{
        this.openSnackBar(value['errorMsg']);
       }
      console.log(this.userInfo);
    });

    this.unSub_signupInfo =  this._store.select('signupInfo').subscribe(value => {
      if(value ==null){
        return;
      }

      if(value['error'] ==''){
        this.signupInfo = value['res'];
        if (this.userInfo['loginType'] == 'google'){
          this.userInfo['mobileNumber'] =   "" + this.signupInfo['mobileNumber'];
        }
        else{
          this.openSnackBar(value['errorMsg']);
        }
      this.openSnackBar('OTP send to your mobile');
      console.log(this.signupInfo);
      }
    });

    this.unSub_resendOTP =  this._store.select('resendOTP').subscribe(value => {
      if(value ==null){
        return;
      }

      if(value['error'] ==''){
      this.openSnackBar('OTP send to your mobile');
      }else{
        this.openSnackBar('OTP not send to your mobile');
      }
    });

    this.unSub_token =  this._store.select('token').subscribe(value => {
      if(value==null){
        return;
      }

      console.log(value['res']);
      if(value['error'] ==''){
        console.log(value['res']['token']);
        this.localStorageService.set("AUTH_TOKEN",""+value['res']['token']);
        let user = this.customerDetail(value['res']['token']);
        console.log(user);
        console.log("ORG TYPE ===> ");
        console.log(user['details']['orgType']);

        console.log(window.location.hostname);
        console.log(window.location.protocol);
        console.log(window.location.href);
        console.log(window.location.port);

        let path='http://'+window.location.hostname+':'+window.location.port;
        console.log(path+"/transporter");
        if(user['details']['orgType'] =='TRANSPORTER'){
          window.location.replace(path+"/transporter");
        }else if(user['details']['orgType'] =='FLEET_OWNER'){
          window.location.replace(path+"/tracknet/vtsdashboard");
        }else if(user['details']['orgType'] =='FLEET_OWNER_AGENT'){
          // window.location.replace(path+"//tracknet/vtsdashboard");
        }
        else if((user['details']['orgType'] == undefined)|| (user['details']['orgType'] == null))
        {
          this._store.dispatch(go(['/addOrganisation']));               // window.location.replace("http://localhost:8080/tracknet/vtsdashboard");
        }
      }else{
        this.openSnackBar(value['errorMsg']);
      }
      // console.log(this.userInfo);
    });

  }// close constructor

  ngOnInit(){

  }

  customerDetail(token){
    let userDetails ={};
    let base64Url = token.split('.')[0];
    let base64 = base64Url.replace('-', '+').replace('_', '/');
    userDetails['userType']=JSON.parse(window.atob(base64));

    base64Url = token.split('.')[1];
    base64 = base64Url.replace('-', '+').replace('_', '/');
    userDetails['details']=JSON.parse(window.atob(base64));
    userDetails['details']['id'] =userDetails['details']['userId'];

    console.log(userDetails);
    return userDetails;
  }


  startTimer(timeInSec){
    this.isResend = false;
    let tempTime = timeInSec - 1;
    this.min = Math.floor(tempTime /60);
    this.sec = Math.floor(tempTime %60);
    this.stopOtpTimer =setInterval(()=>{
      tempTime = tempTime - 1;
      this.min = Math.floor(tempTime /60);
      this.sec = Math.floor(tempTime %60);
      if(tempTime ==0){
        clearInterval(this.stopOtpTimer);
        this.isResend =true;
      }
    },1000);
  }

  fieldsValidation(){
    if(this.userInfo['mobileNumber'] =='' || this.userInfo['mobileNumber'] ==undefined){
      console.log("please fill Mobile No");
      this.openSnackBar("please fill Mobile No");
      return false;
    }

    if(this.otp  ==''  || this.otp ==undefined){
      this.openSnackBar('OTP fill OTP');
      return false;
    }

    return true;
  }

  cancel(){
    // this._store.dispatch({type: "USER_INFO", payload:null});
    // this._store.dispatch(go(['/fretronLogin']));
  }

  login(){
    // let token ='eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCIsImFkbWluIjp0cnVlfQ.eyJpZCI6IjkxZDgwNzAwLTNkYzgtNDdmYS1iOTdjLTExYWE3MGRkZmY1MCIsImVtYWlsIjoic3VuaWxAZnJldHJvbi5jb20iLCJtb2JpbGVOdW1iZXIiOiI5MDg1NDMyOTUxIiwibmFtZSI6IlN1bmlsIiwiaWF0IjoxNTA5MzQ3MDI3fQ.P1-58MzIy39eR9kddIOgkvQy6JQDFP5MutB2i4eulMI';
    // this.localStorageService.set("TOKEN",token);
    // console.log(this._router.url);
    // window.location.replace("http://localhost:8080/tracknet/vtsdashboard");
    if(this.userInfo['userType'] =='new'){
      if(this.signupInfo !=null) {
        let signupObj = {
          "onBoardingType": this.signupInfo['onBoardingType'],
          "mobileNumber": ""+this.signupInfo['mobileNumber'],
          "name": this.signupInfo['name'],
          "otp":this.otp
        };
        if(this.signupInfo['email']!=''){
          signupObj["email"]= this.signupInfo['email'];
        }

        console.log(signupObj);
        this._loginServices.signup.emit(signupObj); // user signup
      }
    }else if(this.userInfo['userType'] =='existing'){
      if(this.fieldsValidation){
          let loginObj ={
            mobileNumber:this.userInfo['mobileNumber'],
            otp:this.otp,
          }

        this._loginServices.login.emit(loginObj); // user login
      }
    }
  }// close login

  resendOTP(){
    console.log("resend");
    if(this.userInfo['mobileNumber'] =='' || this.userInfo['mobileNumber'] ==undefined){
      console.log("please fill Mobile No");
      return false;
    }
    this.startTimer(300);
    let otpType ='';
    if(this.userInfo['userType'] =='new'){
      otpType ='signup'
    }else if(this.userInfo['userType'] =='existing'){
      otpType ='login'
    }
    let obj ={
               mobileNumber:this.userInfo['mobileNumber'],
               otpType:otpType,
             };

    this._loginServices.resendOtp.emit(obj); // call api for checking user existing or not
}// close resend otp

  openSnackBar(message: string){
    this.snackBar.open(message, '', <MdSnackBarConfig>{duration :  3000});
  }
}



