import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";
import {paths} from "../../environments/environment";

@Injectable()
export class LoginServices {
  public defaultHeaders: Headers = new Headers();
  public getUserInfo=new EventEmitter();
  public getSignupOTP=new EventEmitter();
  public resendOtp=new EventEmitter();
  public signup=new EventEmitter();
  public login=new EventEmitter();
  public addUserOrganisation=new EventEmitter();
  public createOrg=new EventEmitter();
  public switchOrganisations=new EventEmitter();
  public token;

  ngOnDestroy(){

  }

  constructor(protected http: Http,private _store:Store<any>,private localStorageService: LocalStorageService){
    // this.defaultHeaders.append('Access-Control-Allow-Origin', '*');
    // this.defaultHeaders.append('Access-Control-Allow-Methods', 'GET,POST, OPTIONS, PUT, PATCH, DELETE');

    this.token=this.localStorageService.get("AUTH_TOKEN");

    //************ get user info ,check user is exix=st or not ***************************************************
    this.getUserInfo.subscribe((reqObj)=> {
       let path =paths.LOGIN_PATH;
      this.getUser(path,reqObj).subscribe(response =>{
        console.log(response);
        let res = response;
        if(res.status >=200 && res.status <300){
          this._store.dispatch({type: "USER_INFO", payload:{error:'',res:res}});
        }else{
          this._store.dispatch({type: "USER_INFO", payload:{error:'X',errorMsg:res.error}});
        }
      },err=>{
        console.log(err);
        console.log(err.json());
        this._store.dispatch({type: "USER_INFO", payload:{error:'X',errorMsg:'error'}});
      });
    });

    //************ signup ***************************************************
    this.getSignupOTP.subscribe((signupObj)=> {
       let path =paths.SIGN_OTP_PATH +signupObj['mobileNumber']+"&otpType="+signupObj['otpType'];
      console.log(path);
      this.getOTPForSignup(path).subscribe(response =>{
        console.log(response);
        if(response.status >=200 && response.status <300){
          this._store.dispatch({type: "SIGNUP_INFO", payload:{error:'',res:signupObj}});
        }else{
          this._store.dispatch({type: "SIGNUP_INFO", payload:{error:'X',errorMsg:"error " +response['error']}});
        }
      },err=>{
        console.log(err);
        console.log(err.json());
        this._store.dispatch({type: "SIGNUP_INFO", payload:{error:'X',errorMsg:'http error'}});
      });
    });

    //************ resend ***************************************************
    this.resendOtp.subscribe((resendObj)=> {
      let path =paths.SIGN_OTP_PATH +resendObj['mobileNumber']+"&otpType="+resendObj['otpType'];
      console.log(path);
      this.getOTPForSignup(path).subscribe(response =>{
        console.log(response);
        if(response.status >=200 && response.status <300){
          this._store.dispatch({type: "RESENDOTP_INFO", payload:{error:''}});
        }else{
          this._store.dispatch({type: "RESENDOTP_INFO", payload:{error:'X',errorMsg:"error " +response['error']}});
        }
      },err=>{
        console.log(err);
        console.log(err.json());
        this._store.dispatch({type: "RESENDOTP_INFO", payload:{error:'X',errorMsg:'http error'}});
      });
    });

    //************ signup ***************************************************
    this.signup.subscribe((signupObj)=> {
      let path =paths.SIGNUP_PATH ;
      console.log(path);
      this.userSignup(path,signupObj).subscribe(response =>{
        console.log(response);
        if(response.status >=200 && response.status <300){
          this._store.dispatch({type: "TOKEN", payload:{error:'',res:response}});
        }else{
          this._store.dispatch({type: "TOKEN", payload:{error:'X',errorMsg:response.error}});
        }
      },err=>{
        console.log(err);
        console.log(err.json());
        this._store.dispatch({type: "TOKEN", payload:{error:'X',errorMsg:'http error'}});
      });
    });

    //************ login ***************************************************
    this.login.subscribe((loginObj)=> {
      let path =paths.CONFIRM_LOGIN_PATH+loginObj['mobileNumber']+"&otp="+loginObj['otp']; ;
      console.log(path);
      this.userLogin(path).subscribe(response =>{
        console.log(response);
        if(response.status >=200 && response.status <300){
          this._store.dispatch({type: "TOKEN", payload:{error:'',res:response}});
        }else{
          this._store.dispatch({type: "TOKEN", payload:{error:'X',errorMsg:response.error}});
        }
      },err=>{
        console.log(err);
        console.log(err.json());
        this._store.dispatch({type: "TOKEN", payload:{error:'X',errorMsg:'http error'}});
      });
    });

    //************ create Organisation ***************************************************
    this.createOrg.subscribe((addOrgObj)=> {
      this.token=this.localStorageService.get("AUTH_TOKEN");
      let path =paths.CREATE_ORGANISATION_PATH ;
      console.log(path);
      this.createOrganisation(path,addOrgObj).subscribe(response =>{
        console.log(response);
        if(response.status >=200 && response.status <300){
          this._store.dispatch({type: "ADD_ORG_RESPONSE", payload:{error:'',res:response}});
        }else{
          this._store.dispatch({type: "ADD_ORG_RESPONSE", payload:{error:'X',errorMsg:response.error}});
        }
      },err=>{
        console.log(err);
        console.log(err.json());
        this._store.dispatch({type: "ADD_ORG_RESPONSE", payload:{error:'X',errorMsg:'http error'}});
      });
    });

    //************ add Organisation to user ***************************************************
    this.switchOrganisations.subscribe((orgId)=> {
      this.token=this.localStorageService.get("AUTH_TOKEN");
      let path =paths.SWITCH_ORG_PATH+orgId;
      console.log(path);
      this.switchOrganisation(path,orgId).subscribe(response =>{
        console.log(response);
        if(response.status >=200 && response.status <300){
          this._store.dispatch({type: "SWITCH_ORG_RESPONSE", payload:{error:'',res:response}});
        }else{
          this._store.dispatch({type: "SWITCH_ORG_RESPONSE", payload:{error:'X',errorMsg:response.error}});
        }
      },err=>{
        console.log(err);
        console.log(err.json());
        this._store.dispatch({type: "SWITCH_ORG_RESPONSE", payload:{error:'X',errorMsg:'http error'}});
      });
    });


  }// constructor close here


  // ***********************  token authorization  **********************
  public getUser(path,reqObj)
  {
    let headers = new Headers();
    headers.set('Content-Type', 'application/json');

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: reqObj,
    });

    return this.http.request(path, requestOptions).map((response: Response) => {
      console.log(response);
      if(response.status==200){
        return response.json();
      }else{
        console.log("not 200");
        return {error:'X'};
      }
    });
  }

  // ***********************  token authorization  **********************
  public userSignup(path,reqObj){
    let headers = new Headers();
    headers.set('Content-Type', 'application/json');

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: reqObj,
    });

    return this.http.request(path, requestOptions).map((response: Response) => {
      console.log(response);
      if(response.status==200){
        return response.json();
      }else{
        console.log("not 200");
        return {error:'X'};
      }
    });
  }

  public createOrganisation(path,addOrgObj){
    let headers = new Headers();
    console.log(this.token);
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: addOrgObj,
    });

    return this.http.request(path, requestOptions).map((response: Response) => {
      console.log(response);
      if(response.status==200){
        return response.json();
      }else{
        console.log("not 200");
        return {error:'X'};
      }
    });
  }

  public switchOrganisation(path,switchOrgId){
    let headers = new Headers();
    console.log(this.token);
    console.log(this.token);
    let bearerToken="Bearer "+this.token;
    headers.set('Content-Type', 'application/json');
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: switchOrgId,
    });

    return this.http.request(path, requestOptions).map((response: Response) => {
      console.log(response);
      if(response.status==200){
        return response.json();
      }else{
        console.log("not 200");
        return {error:'X'};
      }
    });
  }


  // *********************** send otp when user signup********** caled by signupOTP and resend otp************
  public getOTPForSignup(path){
    let headers = new Headers();
    headers.set('Content-Type', 'application/json');

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: reqObj,
    });

    return this.http.request(path,requestOptions).map((response: Response) => {
      console.log(response);
      if(response.status==200){
        return response.json();
      }else{
        console.log("not 200");
        return {error:'X'};
      }
    });
  }

  // *********************** login when user exist************
  public userLogin(path){
    let headers = new Headers();
    headers.set('Content-Type', 'application/json');

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: reqObj,
    });

    return this.http.request(path,requestOptions).map((response: Response) => {
      console.log(response);
      if(response.status==200){
        return response.json();
      }else{
        console.log("not 200");
        return {error:'X'};
      }
    });
  }
}// class close





