import { Component } from '@angular/core';
import {Router} from "@angular/router";
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";
import {MdSnackBarConfig, MdSnackBar} from "@angular/material";
import {LocalStorageService} from "angular-2-local-storage";

@Component({
  selector: 'app-root',
  template: `<router-outlet></router-outlet>`,
})export class AppComponent {

  constructor(private localStorageService: LocalStorageService,private router: Router,private _store:Store<any>,public snackBar: MdSnackBar) {

    // this._store.select('router').subscribe(value => {
    //   // console.log(value);
    //
    //   this.actionService.addAction(value);
    //
    // });

    let token =this.localStorageService.get('AUTH_TOKEN');
    console.log(token);
    if(token ==undefined || token ==null){
      this.openSnackBar("Please Login First !!!");
      this._store.dispatch(go(['/fretronLogin']));
    }


  }

  openSnackBar(message: string){
    this.snackBar.open(message, '', <MdSnackBarConfig>{duration :  4000});
  }
}



